.home-container-1{
    min-height: 100vh;
    max-width: 1250px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin: 0% auto;
}

.home-container-2{
    max-width: 1100px;
    width: calc(100% - 164px);
    border: solid 1px #d6d9dc;
    padding: 24px;
    box-sizing: border-box;
}

@media screen and (max-width: 760px) {
    .home-container-2 {
      max-width: 100%;
      width: 100%;
    }
  }